


export const setCategories = ( payload) => async (dispatch) => {
    try {
        localStorage.setItem("categories",JSON.stringify(payload));
        dispatch({
            type:"SET_CATEGORIES_ITEM",
            payload:payload
        })
    } catch (error) {
    }
  };


