export default function(state = {}, action) {
    switch (action.type) {
      case "SET_CATEGORIES_ITEM":
        return {
          ...state,
          categories: action.payload,
        };
      case "SET_SUB_CATEGORIES_ITEM":
          return {
            ...state,
            subcategories: action.payload,
          };
      case "SET_CATEGORY_NAME_ITEM":
          return {
            ...state,
            categoryName: action.payload,
          };
      default:
        return state;
    }
  }