import { useState,useEffect  } from 'react';
import {useHistory } from "react-router-dom";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import EachSubCategory from './EachSubCategoryComponent';


const SubCategoryContainer=(props)=>{
    let history=useHistory();
    const [subCategoriesList,setSubCategoriesList]=useState([]);
    const baseUrl = '../category/category/';
    useEffect(()=>{
        props.subcategories?setSubCategoriesList(props.subcategories):setSubCategoriesList(JSON.parse(localStorage.getItem("sub-categories")));
    },[props.subcategories])
    return(
        <div className="categoriesContainer">
            <div className="breadcrumb">
                <span className="breadcrumbElement" onClick={()=>history.push('/')}>HOME &nbsp;&nbsp;/</span>
                <span onClick={()=>history.push('/categories')} >&nbsp;&nbsp;Equipment Catalog &nbsp;&nbsp;/</span>
                <span>&nbsp;&nbsp;{props.match.params.name}</span>
            </div>
            {subCategoriesList.map((category,i)=>(
                <EachSubCategory key={i} category={category}/>
            ))}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        subcategories:state.machine.subcategories
    };
  };
  
const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        // setCategories: setCategories,
    }, dispatch);
  }
export default connect(mapStateToProps,mapDispatchToProps)(SubCategoryContainer);
