
import {Provider} from 'react-redux';
import {BrowserRouter as Router,Switch,Route} from 'react-router-dom';

import './App.css';
import store from './Store';
import Header from './common/Header';
import MainPageContainer from './container/MainPageContainer';
import CategoryContainer from './container/CategoryContainer';
import SubCategoryContainer from './container/SubCategoryContainer';

function App() {
  return (  
    <Provider store={store}>
      <Router>
        <div className='App'>
            <Header/>
            <Switch>
              <Route exact={true} path='/' component={MainPageContainer} />
              <Route exact={true} path='/categories' component={CategoryContainer} />
              <Route exact={true} path='/sub-categories/:name' component={SubCategoryContainer} />
            </Switch>
          <div className="footer"></div>
        </div>  
      </Router>
    </Provider>
  );
}

export default App;
