
import { useHistory } from "react-router-dom";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import { setSubCategories, setCategoryName } from '../Store/Actions/machine/machineAction';

const images = require.context('../category', true);
const EachCategory=(props)=>{
    const history = useHistory();
    let img = '';
    try{
        img=images('./' + props.category.image);

    }
    catch(e){
        img={default:"https://anokha.world/images/not-found.png"};
    }
    const setSubcategory=()=>{
        props.setSubCategories(props.category.subcategories);
        history.push('/sub-categories/'+props.category.name);
    }
    return(
        <div className="eachCategory">
                    <div className="image-container">
                        <img src={img.default} alt="image not found"/>
                    </div>
                    <div className="name-container" onClick={()=>setSubcategory()}>
                        {props.category.name}
                        <span><i className="arrow right"></i></span>
                    </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
    };
  };
  const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        setSubCategories: setSubCategories,
    }, dispatch);
  }
    export default connect(mapStateToProps,mapDispatchToProps)(EachCategory);