import { useHistory } from "react-router-dom";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import { setSubCategories } from '../Store/Actions/machine/machineAction';

const images = require.context('../category/subcategory', true);
const EachSubCategory=(props)=>{
    let img = '';
    try{
        img=images('./' + props.category.image);

    }
    catch(e){
        img={default:"https://anokha.world/images/not-found.png"};
    }
    return(
        <div className="eachCategory">
                    <div className="image-container">
                        <img src={img.default} alt={"image not found"}/>
                    </div>
                    <div className="name-container" >
                        {props.category.name}
                    </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
    };
  };
  const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        setSubCategories: setSubCategories,
    }, dispatch);
  }
    export default connect(mapStateToProps,mapDispatchToProps)(EachSubCategory);