import {useEffect, useState} from 'react';
import { useHistory } from "react-router-dom";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import { setCategories } from '../Store/Actions/machine/machineAction';
import Data from '../jsonData/catalog .json';


const Header=(props)=>{
    const [data,setData]=useState(Data.data);
    const [location,selectLocation]=useState(Data.data.locations);
    const history = useHistory();

    const setCategoryByLocation=(location)=>{
      let categories=[];
      location.branches.forEach(element => {
        categories.push(...element.categories);
      });
      props.setCategories(categories);
      history.push('/categories');
    }
  
    const setCategoryByBranch=(branch)=>{
      let categories=[];
      categories.push(...branch["categories"]);
      props.setCategories(categories);
      history.push('/categories');
    }
    return(
        <div className="headerAndLocation">
            <div className="heading">RENTAL MANAGEMENT SYSTEM</div>
            <div className="selectLocation"><span>SELECT LOCATION</span> 
            <ul  className="showLocations">
                {location.map((location,i)=>(
                <li className="eachLocation" key={i}>
                    <span onClick={()=>setCategoryByLocation(location)}>{location.name}</span>
                    <div className="sub-menu">
                    {location.branches.map((branch,j)=>(
                        <section key={j} onClick={()=>setCategoryByBranch(branch)}>{branch.name}</section>
                    ))}
                    </div>
                </li>
                ))}
            </ul>
            </div>
        </div>
    )
}
const mapStateToProps = state => {
    return {
    };
  };
  const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        setCategories: setCategories,
    }, dispatch);
}
  export default connect(mapStateToProps,mapDispatchToProps)(Header);