
import { useState,useEffect  } from 'react';
import {useHistory } from "react-router-dom";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import EachCategory from './EachCategoryComponent';

const CategoryContainer=(props)=>{
    let history=useHistory();
    const [categoriesList,setCategoriesList]=useState([]);
    const baseUrl = '../category/category/';
    useEffect(()=>{
        props.categories?setCategoriesList(props.categories):setCategoriesList(JSON.parse(localStorage.getItem("categories")));
    },[props.categories])
    return(
        <div className="categoriesContainer">
            <div className="breadcrumb">
                <span className="breadcrumbElement" onClick={()=>history.push('/')}>HOME &nbsp;&nbsp;/</span>
                <span>&nbsp;&nbsp;Equipment Catalog </span>
            </div>
            {categoriesList.map((category,i)=>(
                <EachCategory key={i} category={category}/>
            ))}
        </div>
    )
}
const mapStateToProps = state => {
    return {
        categories:state.machine.categories
    };
  };
  const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        // setCategories: setCategories,
    }, dispatch);
  }
    export default connect(mapStateToProps,mapDispatchToProps)(CategoryContainer);